.form-tender__list {
    margin-bottom: 64px; 
}
.form-tender__list:nth-of-type(1) {
    border: none;
    width: 431px;
}
.form-tender__list:nth-of-type(2) {
    max-width: 431px;
}
.form-tender input {
    height: 59px;
}
.form-tender__list:nth-of-type(3) {
    width: 100%;
}
.form-tender__list:nth-of-type(5) {
    width: 100%;
}
.form-tender__list:nth-of-type(6) {
    width: 100%;
    border: none;
}
.form-tender__title {
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #D22856;
    text-align: center;
    text-transform: uppercase;
}
