.reviews {
    text-align: center;
    position: relative;
    @media (min-width: @desktop-width) {
        display: block;
    }
}
.reviews__item {
    margin: 0;
    padding: 0 20px;
    @media (min-width: @desktop-width) {
    }
}
.reviews__image {
    display: none;
    @media (min-width: @desktop-width) {
        display: block;
        grid-area: image;
        justify-self: center;
        padding: 72px 0 58px;
    }
}
.reviews__text {
    order: 2;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    margin: 0;
    margin-bottom: 17px;
    @media (min-width: @tablet-width) {
        order: 1;
        font-size: 18px;
        line-height: 30px;
    }
    & br {
        display: none;
        @media (min-width: @desktop-width) {
            display: block;
        }
    }
    @media (min-width: @desktop-width) {
        order: 2;
        grid-area: text;
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
        color: #283645;
        width: 688px;
        justify-self: center;
    }
}
.reviews__author-info {
    order: 1;
    @media (min-width: @tablet-width) {
        order: 2;
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 20px;
    }
    @media (min-width: @desktop-width) {
        display: grid;
        justify-items: center;
        margin: 25px 0 0 0;
    }
}
.reviews__experience {
    display: none;
    @media (min-width: @tablet-width) {
        display: block;
        order: 3;
        font-size: 14px;
        line-height: 30px;
        margin: 0;
    }
    @media (min-width: @desktop-width) {
        font-size: 18px;
    }
}
.reviews__author-name {
    font-weight: 700;
    font-size: 24px;
    @media (min-width: @tablet-width) {
        margin: 20px 0 0;
    }
    @media (min-width: @desktop-width) {
        font-size: 24px;
    }
}
.reviews__wrapper {
    background: @lightwhite;
    margin-top: -65px;
    @media (min-width: @tablet-width) {
        padding-bottom: 20px;   
        margin-top: 0;
    }
}

.reviews__list {
    position: relative;
    margin-bottom: 15px;
}
.reviews__manipulate {
    position: absolute;
    top: 46%;
    left: 95px;
    width: 82%;
    display: flex;
    justify-content: space-between;
}
.reviews__prev,
.reviews__next{
    display: none;
    @media (min-width: @desktop-width) {
        display: block;
        color: rgba(40, 54, 69, 0.3);
        &:hover{
            color: rgba(40, 54, 69, 0.6);
        }
        &:active{
            color: rgba(40, 54, 69, 0.1);
        }
    }
}
.reviews__toggles {
    margin: 0;
    display: flex;
    @media (min-width: @tablet-width) {
        margin-bottom: -16px;
    }
    @media (min-width: @desktop-width) {
        display: none;
    }
}
.reviews__author-item {
    visibility: hidden;
    position: absolute;
}
.reviews__author-item--visible {
    visibility: visible;
    position: inherit;
}
.reviews__author-name {
}
.reviews__experience {
}
