.advantages {
    font-family: 'Open Sans';
}
.advantages__column {
    margin-top: 29px;
    @media (min-width: @desktop-width) {
    margin-top: 0;
    } 
}
.advantages__wrapper {
    min-width: 320px;
    margin: 0 auto; 
}
.advantages__text {
    margin: 0 auto;
    max-width: 280px;
    font-weight: 300;
    font-size: 24px;
    text-align: center;
    color: #283645;
    &:nth-of-type(1) {
        padding-top: 18px;
        margin-bottom: 31px;
        @media (min-width: @desktop-width) {
            padding-top: 63px;
        }
    }
    @media (min-width: @tablet-width) {
        min-width: 588px;
        font-size: 40px;
        line-height: 54px;
        &:nth-of-type(1) {
            margin-bottom: 0;
        }
    }  
    @media (min-width: @desktop-width) {
        min-width: 780px;
        font-size: 50px;
        line-height: 68px;
    }  
}
.advantages__row {
    background: #FFFFFF;
}
.advantages__clock {
    @media (min-width: @tablet-width) {
        float: left;
        }
    @media (min-width: @desktop-width) {
        }
}
.advantages__title {
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    color: #000000;
    @media (min-width: @tablet-width) {
        text-align: left;
        line-height: 36px;
        grid-column: 2 / 3;
        grid-row: 1 / 2;
        margin: 0;
        padding-top: 19px;
    }
    @media (min-width: @desktop-width) {
        grid-row: 1 / 2;
        padding-top: 0;
        display: flex;
        flex-direction: column-reverse;
        font-size: 30px;
        margin-bottom: 26px;
        justify-self: baseline;
        & > br {
            display: none;
        }
    }
}
.advantages__description { 
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: #283645;
    & > br { 
        display: none;
        @media (min-width: @desktop-width) {
            display: block;
        }
    }
    & > br:nth-of-type(3) { 
        display: none;
        @media (min-width: @tablet-width) {
            display: block;
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    @media (min-width: @tablet-width) {
        font-size: 14px;
        line-height: 24px;
        grid-column: 2 / 3;
        grid-row: 2 / 3;
        }
    @media (min-width: @desktop-width) {
        justify-self: baseline;
        grid-row: 2 / 3;
        margin: 0;
        font-size: 18px;
        line-height: 30px;
    }
}
.advantages__link {
    display: inline-block;
    text-align: right;
    font-weight: 400;
    line-height: 24x;
    text-decoration: none;
    margin-bottom: 0;
    @media (min-width: @tablet-width) {
        font-size: 14px;
        grid-column: 2 / 3;
        grid-row: 3 / 4;
        text-align: left;
        width: 300px;
        }
    @media (min-width: @desktop-width) {
        justify-self: flex-start;
        grid-row: 3 / 4;
        font-size: 18px;
        line-height: 30px;
    }
}
.advantages__link--selected {
    color: #D22856;
    text-decoration-line: underline;
}
.advantages__time {
    display: none;
    @media (min-width: @tablet-width) {
        position: relative;
        margin-top: -84px;
        padding: 70px 20px 0px 20px;
        background: #FFFFFF;
        display: grid;
        grid-template-columns: 238px 1fr;
        grid-template-rows: 85px 1fr 46px;
        column-gap: 72px;
        justify-content: left;
        margin-bottom: 109px;
    }
    @media (min-width: @desktop-width) {
        
        position: inherit;
        grid-template-columns: 300px 1fr;
        grid-template-rows: 131px 1fr 29px;
        column-gap: 70px;
        padding: 83px 130px 106px 130px;
        background: #f2f2f2;
        margin-bottom: 0;
    }
}
.advantages__clock {
    @media (min-width: @tablet-width) {
        text-align: center;
        margin-top: -20px;
        grid-column: 1 / 2;
        grid-row: 1 / 4;
        }
    @media (min-width: @desktop-width) {
        width: 298px;
    }
}
