.page-footer__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 20px;
    @media(min-width:@tablet-width){
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
    @media(min-width:@desktop-width){
        padding: 70px 130px 75px 130px;
        justify-content: space-evenly;
    }
}
.page-footer__logo {
    margin-bottom: 35px;
    @media(min-width:@tablet-width){
        margin-bottom: 0;
        margin-top: -15px;
    }
    @media(min-width:@desktop-width){
        margin-right: 240px;
    }
}
.page-footer__social {
    margin-bottom: 35px;
    @media(min-width:@tablet-width){
        margin: 0;
        margin-left: 80px;
        margin-right: 92px;
    }
    @media(min-width:@desktop-width){
        margin: 0;
    }
}
.page-footer  .social__list {
    margin: 0;
    text-decoration: none;
    display: flex;
    list-style-type: none;
    flex-wrap: wrap;
    width: 178px;
}
.page-footer__copyright {
    @media(min-width:@desktop-width){
        margin-left: auto;
    }
}
.page-footer__logo-desktop {
    display: none;
    @media(min-width: @desktop-width) {
      width: 147px; height: 40px;
      display: block;
    }
}
.page-footer__logo-mobile {
    width: 145px; height: 40px;
    @media(min-width: @desktop-width) {
      display: none;
    }
}
.page-footer__logo-html {
    width: 27px; height: 34px;
}
