.upload {
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 18px;
}
.upload__wrapper {
 background: #F2F2F2;
    @media (min-width: @desktop-width) {
        width: 940px;
        margin: 0 auto;
    }
}
.upload__edit {
    @media (min-width: @tablet-width) {
        display: grid;
        grid-template-columns: 300px 300px;
        grid-template-rows: 200px;
        & .image-editor__image {
            grid-column: 1 / 2;
            grid-row: 1 / 3;
        }
        & .image-editor__wrapper {
            grid-column: 2 / 3;
            grid-row: 1 / 2;
            grid-column-gap: 43px;
            @media (min-width: @desktop-width) {
                grid-column-gap: 88px;
            }
        }
        & .image-editor__buttons {
            grid-column: 2 / 3;
            grid-row: 2 / 3;
        }
    }
    @media (min-width: @tablet-width) {
        padding: 57px 20px 0px 20px;
        }
    @media (min-width:@desktop-width) {
        grid-column-gap: 60px;
        padding: 55px 80px;
        grid-template-columns: 300px 410px;
        grid-template-rows: 200px 200px;
    }
}
.upload__text {
    font-size: 14px;
    line-height: 30px;
    line-height: 30px;
    text-align: center;
    color: #283645;
    background: #ffffff;
    @media (min-width: @tablet-width) {
        font-size: 18px;
        line-height: 30px;
        margin-top: 78px;
        margin-bottom: 73px;
    }
    @media (min-width: @desktop-width) {
        margin-top: 64px;
        margin-bottom: 59px;
    }
}
