
.base{
    display: none;
    @media (min-width: @tablet-width) {
        position: relative;
        display: inline-block;
        width: 59px;
        height: 59px;
        background: #E5E5E5;
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}
