.tender__checkbox > span::before {
  content: '';
  display: inline-block;
  width: 35px;
  height: 35px;
  flex-shrink: 0;
  flex-grow: 0;
  margin-right: 22px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  z-index: 0;
  background: #FFFFFF;
  border: 2px solid #E5E5E5;
  border-radius: 2px;
}
.tender__radio > span::before {
  content: '';
  display: inline-block;
  width: 37px;
  height: 37px;
  flex-shrink: 0;
  flex-grow: 0;
  margin-right: 31px;
  background-size: 50% 50%;
