.app {
    font-family: 'Open Sans';
    color: #FFFFFF;
    font-size: 24px;
    line-height: 33px;
}
.app__header {
    background: #000000 url(../../img/background/back-mobile.jpg) fixed no-repeat;
    background-size: 100%;
    @media (min-width: @tablet-width) {
        background: url(../../img/background/tablet-phone.png) 100% 100% no-repeat,
                    url(../../img/background/back-tablet.jpg) content-box  no-repeat;
        height: 735px;
        background-size: auto, cover;
      }
    @media (min-width: @desktop-width) {
        background: url(../../img/background/phone-desktop.png) 100% 100% no-repeat,
                    url(../../img/background/back-desktop.jpg) content-box no-repeat;
        height: 856px;
        background-size: auto, cover;
    }
}
.app-header__wrapper {
    padding-top: 65px;
    @media (min-width: @tablet-width) {
      width: 300px;
      padding-top: 170px;
      padding-left: 20px;
    }
    @media (min-width: @desktop-width) {
        padding-left: 131px;
        padding-top: 209px;
    }
}
.app-header__wrapper {
    display: flex;  
    flex-direction: column;
}
.app-header {
    font-family: 'Open Sans';
    color: #FFFFFF;
}
.app-header__slogan {
    margin-top: 10px;
    grid-area: "title";
    margin-bottom: 10px;
    font-weight: 300;
    font-size: 24px;
    line-height: 120%;
    text-align: center;
    padding: 0 20px;
    & br:nth-of-type(3) {
        display: none;
    }
    @media (min-width: @tablet-width) {
        font-size: 40px;
        line-height: 48px;
        margin-top: 36px;
        & > br {
            display: block;
        }
    }
    @media (min-width: @desktop-width) {
        font-weight: 300;
        font-size: 50px;
        line-height: 68px;
        padding: 0;
        margin: 0;
        margin-bottom: 40px;
    }
}
.app-header__photo img {
    display: block;
    vertical-align: middle;
}
.app-header__photo {
    margin: 0 auto;
    @media (min-width: @tablet-width) {
        display: none;
    }  
}
.app-header__container {
    grid-area: "container";
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 28px 20px 32px 20px;
    background: #1D2631;
    @media (min-width: @tablet-width) {
        background: none;
        padding-top: 0;
    }
    @media (min-width: @desktop-width) {
        padding: 0;
        margin-left: -15px;
    }
}
.app-header__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: 0;
    @media (min-width: @tablet-width) {
        padding-left: 33px;
        margin-bottom: 25px;
    }
    @media (min-width: @desktop-width) {
        margin-bottom: 45px;
        margin-top: 0;
        padding-left: 47px;
    }
}
.app-header__item {
    &:first-child{
        margin-left: -35px;
    }
    &:not(:last-child){
        margin-right: 35px;
    }
    &:last-child{
        margin-bottom: -8px;
    }
}
.app-header__list{
    list-style-type: none;
    text-decoration: none;
}
.app-header__image {
    display: inline-block;
}
.app-header__text{
    font-family: 'Open Sans';
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #ffffff;
    margin: 0;
    @media (min-width: @tablet-width) {
        font-size: 18px;
        width: 300px;
    }
}
.app-header__apple {
    width: 47px;
    height: 56px;
}
.app-header__android {
    width: 43px;
    height: 47px;
}
.app-header__windows {
    width: 44px;
    height: 41px;
}
