.social__link {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 48px;
    color: #D22856;
    border: 3px solid rgba(40, 54, 69, 0.1);
    border-radius: 50%;
    &:hover{
        border: 3px solid #283645;
    }
    &:active{
        border: 3px solid rgba(40, 54, 69, 0.3)
    }
}
.social__list {
    padding: 0;
}
.social__item:not(:last-child) {
    margin-right: 8px;
}
.social__link svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
