.contacts {
    font-family: 'Open Sans';
    background-color: #ffffff;
}
.contacts__wrapper {
    margin: 0 auto;
    text-align: center;
    padding: 40px 0 0px 0;
    @media(min-width:@desktop-width){
        padding-top: 55px;
    }
}
.contacts__text {
    font-weight: 300;
    font-size: 24px;
    line-height: 28px;
    color: #283645;
    margin: 0 0 40px;
    @media(min-width:@tablet-width){   
        font-size: 40px;
        line-height: 54px;
    }
    @media(min-width:@desktop-width){
        font-size: 50px;
        line-height: 68px;
    }
}
.contacts__list {
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    @media(min-width:@tablet-width){
        position: relative;
        display: grid;
        width: 100%;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        padding: 0;
        z-index: 1;
    }
    @media(min-width:@desktop-width){
        grid-template-columns: 206px 211px 241px;
        padding: 0 0 59px 177px;
        gap: 105px;
        align-items: baseline;
    }
}
.contacts__item {
    margin-bottom: 25px;
    list-style-type: none;
    & p {
        margin: 0;
    }
    @media(min-width:@tablet-width){
        margin-bottom: 41px;
    }
    @media(min-width:@desktop-width){
        margin-bottom: 25px;
    }
}
.contacts__item:nth-of-type(1) {
    order: 1;
    @media(min-width:@tablet-width){
        order: 1;
        grid-column: 1 / 2;
        grid-row: 1 / 2;
    }
    @media(min-width:@desktop-width){
        order: 1;
    }
}
.contacts__item:nth-of-type(2) {
    z-index: 2;
    order: 3;
    @media(min-width:@tablet-width){
        order: 3;
        grid-column: 1 / 3;
        grid-row: 2 / 3;
        align-self: center;
        padding-bottom: 25px;
    }
    @media(min-width:@desktop-width){
        order: 2;
        grid-column: 2 / 3;
        grid-row: 1 / 2;
        padding-bottom: 0;
    }
}
.contacts__item:nth-of-type(3) {
    order: 2;
    @media(min-width:@tablet-width){
        order: 2;
        grid-column: 2 / 3;
        grid-row: 1 / 2;
    }
    @media(min-width:@desktop-width){
        order: 3;
        grid-column: 3 / 4;
        grid-row: 1 / 2;
    }
}
.contacts__title {
    color: #283645;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    @media(min-width:@desktop-width){
        display: block;
        margin-bottom: 30px;
    }
}
.contacts__address {
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    color: #D22856;
    text-decoration: none;
    @media(min-width:@tablet-width){
        display: inline-block;
        width: 211px;
    }
    &:link {
        color: #D22856;
    }
    &:hover {
        color: #283645;
    }
    &:active {
        color: #283645;
        opacity: 0.3;
    }
}
.contacts__wrap-map {
    @media(min-width:@tablet-width){
        width: 660px;
        height: 374px;
    }
    @media(min-width:@desktop-width){
        width: 1200px;
        height: 502px;
        background-position: top left;
    }
}
.contacts .contacts__map {
    position: relative;
    margin-top: -100px;
}
.contacts__marker {
    position: absolute;
    top: 186px;
    left: 142px;
    @media(min-width:@tablet-width){
        top: 218px;
        left: 309px;
    }
    @media(min-width:@desktop-width){
        top: 271px;
        left: 581px;
    }
}
