.site-list {
    @media(min-width:@desktop-width){
        display: flex;
        align-items: start;
        flex-wrap: wrap;
    }
}
.site-list__item{
    @media(min-width:@desktop-width){
        margin-right: 78px;
        &:last-child {
            margin-right: 0;
        }
    }
}
.site-list a {
    position: relative;
    display: block;
    padding-top: 25px;
    padding-bottom: 25px;
    text-align: center;
    line-height: 24px;
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    background-color: #283645;
    border-top: 1px solid rgba(255, 255, 255, 0.15);
    border-bottom: 1px solid #000000;
    font-weight: 400;
    @media (min-width: @desktop-width){
            display: inline-block;
            text-transform: none;
            background-color: rgba(0, 0, 0, 0);
            border: none;
            font-size: 18px;
    }
  }
.site-list .site-list__link:hover{
    color: #D22856;
}
  .site-list .site-list__link:active{
    color: fade(#D22856, 30%);
}
.site-list__item--active{
    @media (min-width: @desktop-width){
        position: relative;
        color: #ffffff; 
        cursor: pointer;
        line-height: 1; 
        text-decoration: none;
        &::after {
            display: block;
            position: absolute;
            left: 0; 
            bottom: 17px;
            width: 100%;
            height: 2px; 
            background-color: #ffffff; 
            content: "";
        }
    }
}
