.panorama {
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 18px;
}
.panorama__header {
    background: #000000 url(../../img/background/back-mobile.jpg) no-repeat;
    @media (min-width: @tablet-width){
        background: #000000 url(../../img/background/back-tablet.jpg) no-repeat;
    }
    @media (min-width: @desktop-width) {
        background: #000000 url(../../img/background/back-desktop.jpg) no-repeat;
    }
}
.panorama__main-title {
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
    color: #FFFFFF;
    padding: 102px 72px 69px 72px;
    margin: 0;
    @media (min-width:@tablet-width) {
        font-weight: 300;
        font-size: 50px;
        line-height: 68px;
        padding: 190px 0 143px 0;
    }
    @media (min-width:@desktop-width) {
        padding: 190px 0 143px 0;
    }
}
.panorama-catalog__title {
    font-family: 'Open Sans';
    padding: 0;
    margin: 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
}
.panorama-catalog__header-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    text-align: center;
    color: #283645;
    padding: 42px 30px 60px 38px;
    margin: 0 auto;
    @media (min-width: @tablet-width) {
        font-size: 18px;
        line-height: 30px;
        padding: 100px 0px 85px 0px;
    }

}
.panorama-catalog__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #283645;
    margin: 0;
    margin-bottom: 12px;
    @media (min-width: @tablet-width) {
        flex-grow: 1;
        margin-bottom: 0;
    }
}
.panorama-catalog__container {
    margin: 0;
    width: 280px;
    padding: 0 20px;
    list-style-type: none;
    @media (min-width: @tablet-width) {
        display: grid;
        grid-template-columns: repeat(2, 300px);
        grid-gap: 20px;
    }
    @media (min-width: @desktop-width) {
        grid-template-columns: repeat(3, 300px);
        padding: 0 130px;
        grid-row-gap: 54px;
        grid-column-gap: 20px;
    }
}
.panorama-catalog__card{
    background: #F2F2F2;
    @media (min-width: @tablet-width) {
        &:nth-of-type(1)  {
            order: 1;
            grid-column: 1 / 3;
            & .panorama-catalog__card__wrap {
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: space-between;

                & .panorama-catalog__title {
                    order: 0;
                }
                & .panorama-catalog__like {
                    order: 0;
                }
                & .panorama-catalog__text {
                    order: 1;
                }
            }
            @media (min-width: @desktop-width) {
                grid-column: 1 / 4;
            }
        }
        &:nth-of-type(2)  {
            order: 2;
            grid-column: 1 / 2; 
        }
        &:nth-of-type(3)  {
            order: 3;
            grid-column: 2 / 3;
        }
        &:nth-of-type(4)  {
            order: 4;
            grid-column: 1 / 2;
            @media (min-width: @desktop-width) {
                grid-column: 3 / 4;
            }
        }
        &:nth-of-type(5)  {
            order: 5;
            grid-column: 2 / 3;
            @media (min-width: @desktop-width) {
                grid-column: 1 / 2;
            }
        }
        &:nth-of-type(6)  {
            order: 6;
            grid-column: 1 / 2;
            @media (min-width: @desktop-width) {
                grid-column: 2 / 3;
            }
        }
        &:nth-of-type(7)  {
            order: 7;
            grid-column: 2 / 3;
            @media (min-width: @desktop-width) {
                grid-column: 3 / 4;
            }
        }
    }
}
.panorama-catalog__card__wrap{
    padding: 34px 0 20px 20px;
    &:not(:first-child) {
        padding: 15px 0 20px 20px;
        @media (min-width: @tablet-width) {
            height: 115px;
            padding-top: 18px;
        }
    }
    @media (min-width: @tablet-width) {
        padding: 31px 31px 30px 30px;
        display: flex;
        flex-direction: column;
    }
    & br {
        @media (min-width: @tablet-width) {
            display: none;
        }
        @media (min-width: @desktop-width) {
        }
    }
}

.panorama-catalog__card:not(:last-child) {
    margin-bottom: 20px;
    @media (min-width: @tablet-width) {
        margin-bottom: 0;
    }
}
.panorama-catalog__link {
    text-decoration: none;
}
.panorama-catalog__link--main {
    position: relative;
    display: block;
    height: 119px;
    @media (min-width: @tablet-width) {
        height: 509px;
        background-image: url(../../img/catalog/panorama-tablet.jpg);
        width: 660px;
    }
    @media (min-width: @desktop-width) {
        background-image: url(../../img/catalog/panorama-desktop.jpg);
        height: 506px;
        width: 1200px;
    }
}
.panorama-catalog__link--main .panorama-catalog__image {
    position: absolute;
    background-color: #f2f2f2;
    @media (min-width: @tablet-width) {
        display: none;
    }
}
.panorama-catalog__label {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    content: "";
    top: 0;
    right: 20px;
    text-align: center;
    width: 140px;
    height: 36px;
    background: #D22856;
    font-weight: 700;
    font-size: 12px;
    color: #FFFFFF;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
        top: 57px;
    }
    @media (min-width: @desktop-width) {
        top: 54px;
    }
}
.panorama-catalog__time {
    font-family: 'Open Sans';
    font-weight: 300;
    font-size: 12px;
    line-height: 24px;
    text-transform: uppercase;
    color: #283645;
}
.panorama-catalog__card__wrap .panorama-catalog__heart {
    padding: 12px 10px;
    min-width: 40px;
    min-height: 34px;
    font-family: inherit;
    font-weight: bold;
    font-size: 16px;
    border: none;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: 12px;
    cursor: pointer
}

.panorama-catalog__heart .panorama-catalog__number {
    margin-left: 24px;
    line-height: 14px;
  }
.panorama-catalog__heart {
    color: #D22856;
    background-image: url("/img/icon-heart.svg");
    opacity: 0.7;
  }

.panorama-catalog__heart.panorama-catalog__heart--added {
    background-image: url("/img/icon-heart-light.svg");
    opacity: 1;
  }
  
.panorama-catalog__heart:hover {
outline-color: red;
}
.panorama-catalog__heart p {
    padding-left: 30px;
}
.panorama-catalog__number {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    color: #D22856;
}
