.image-editor__wrapper {
    padding: 0px 0px 0px 3px;
}
.image-editor__wrapper {
    @media (min-width:@tablet-width) {
        display: grid;
        grid-template-columns: 30px 202px;
        align-content: center;
        & .image-editor__fill:nth-of-type(1) {
            order: 1;
            grid-column: 1 / 2;
        }
        & .image-editor__fill:nth-of-type(2) {
            order: 3;
            grid-column: 1 / 2;
        }
        & .image-editor__fill:nth-of-type(3) {
            order: 5;
            grid-column: 1 / 2;
        }
        & .image-editor__scale:nth-of-type(1) {
            order: 2;
            grid-column: 2 / 3;
        }
        & .image-editor__scale:nth-of-type(2) {
            order: 4;
            grid-column: 2 / 3;
        }
        & .image-editor__scale:nth-of-type(3) {
            order: 6;
            grid-column: 2 / 3;
        }
    }
}
.image-editor__fill--active svg {
    color: rgba(210, 40, 86, 1);
    &:hover{
        color: #283645;
        opacity: 0.5;
    }
    &:active{
        color: #283645;
        opacity: 0.3;
    }
    &:nth-of-type(2),
    &:nth-of-type(3){
        display: none;
        @media (min-width:@tablet-width) {
            display: block;
        }
    }
}
.image-editor__fill{
    color: rgba(40, 54, 69, 0.3);
    &:hover{
        color: #D22856;
        opacity: 1;
    }
    &:active{
        color: #283645;
        opacity: 0.3;
    }
    @media (min-width: @tablet-width) {
        color: #D22856;
        position: relative;
        top: 26px;
        left: 29px;
    }
    @media (min-width: @desktop-width) {
        top: 19px;
        left: 26px;
    }
}
.image-editor__fill-size-1 {
    @media (min-width: @tablet-width) {
        width: 22px;
        height: 22px;
    }
    @media (min-width: @desktop-width) {
        width: 44px;
        height: 44px;
    }
}
.image-editor__fill-size-2 {
    @media (min-width: @tablet-width) {
        width: 24px;
        height: 22px;
    }
    @media (min-width: @desktop-width) {
        width: 48px;
        height: 43px;
    }
}
.image-editor__fill-size-3 {
    @media (min-width: @tablet-width) {
        width: 16px;
        height: 16px;
    }
    @media (min-width: @desktop-width) {
        width: 32px;
        height: 32px;
    }
}
.image-editor__fill:not(:last-child) {
    margin-right: 41px;
    display: inline-block;
    transform: translate(100%);
    }
    .image-editor__fill:first-child {
        margin-right: 32px;
    }

.image-editor img {
    padding: 20px 20px 42px 20px;
    @media (min-width: @tablet-width) {
        padding: 0;
    }
    @media (min-width: @desktop-width) {
        width: 300px;
    }
}
.image-editor__image-fill {
    fill: #283645;
}
.image-editor__scale {
    position: relative;
    height: 2px;
    background-color: #283645;
    margin: 33px auto;
    display: none;
    width: 215px;
    @media (min-width: @tablet-width){
        display: block;
        color: rgba(210, 40, 86, 1);
        margin: 36px 0 42px;
    }
    @media (min-width: @desktop-width){
        margin-bottom: 30px;
        width: 288px;
    }
}
.image-editor__scale--active {
    display: block;
}
.image-editor__range-bar {
    position: absolute;
    height: 2px;
}
.image-editor__range-toggle {
    position: absolute;
    width: 14px;
    height: 14px;
    background: #FFFFFF;
    border: 2px solid #D22856;
    border-radius: 50%;
    cursor: pointer;
}
.image-editor__range-toggle:hover {
    background: #D22856;
  }
.image-editor__range-toggle:active,
.image-editor__range-toggle:focus {
background: #D22856;
}
.image-editor__buttons {
    border-top: 1px solid rgba(40, 54, 69, 0.1);
    padding: 53px 0px 59px 31px;
    @media (min-width: @tablet-width) {
        border-top: none;
    }
    @media (min-width: @desktop-width) {
        display: flex;
        width: 410px;
        padding: 0;
        margin-top: auto;
        justify-content: space-between;
    }
}
.image-editor__button {
    width: 260px;
    height: 55px;
    border: 3px solid #D22856;
    border-radius: 30px;
    margin-bottom: 12px;
    color: #D22856;
    font-size: 18px;
    font-weight: 700;
    @media (min-width: @tablet-width) {
        width: 260;
    }
    @media (min-width: @desktop-width) {
        width: 183px;
        margin-right: 20px;
        margin-bottom: 0;
    }
    &:hover{
        background: rgba(210, 40, 86, 1);
        color: #FFFFFF;
    }
    &:active{
        background: rgba(210, 40, 86, 1);
        color: rgba(255, 255, 255, 0.3);
    }
}
.image-editor__button:disabled {
    border: 3px solid rgba(40, 54, 69, 0.1);
    cursor:inherit;
    color: rgba(40, 54, 69, 0.1);

    &:hover{
        color: rgba(40, 54, 69, 0.3);
        background: #F2F2F2;
        border: 3px solid rgba(40, 54, 69, 0.3);
    }
    &:active{
        color: rgba(255, 255, 255, 0.3);
        background: rgba(40, 54, 69, 0.3);
        border: none;
    }
    @media (min-width: @desktop-width) {
        margin-right: 0;
    }
}
