.modal {
    position: relative;
    top: 5%;
    font-family: 'Open Sans';
    margin: auto;
    background-color: #FFFFFF;
    box-shadow: 0px 7px 15px rgba(0, 1, 1, 0.15);
    border-radius: 1px;
    z-index: 5;
    width: 320px;
    @media (min-width: @tablet-width) {
        width: 460px;
    }
    @media (min-width: @desktop-width) {
        top: 30%;
        right: 9%;
        width: 780px;
    }
  }
.modal__error {
    padding: 44px 20px 52px;
    & .modal__text br:nth-of-type(1){
        display: none;
        @media (min-width: @tablet-width) {
            display: block;
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    & .modal__text br:nth-of-type(3){
        display: none;
        @media (min-width: @tablet-width) {
            display: block;
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    & .modal__text br:nth-of-type(2) {
        display: block;
        @media (min-width: @tablet-width) {
            display: none;
        }
        @media (min-width: @desktop-width) {
            display: block;
        }
    }
    & .modal__text {
        @media (min-width: @desktop-width) {
            margin-bottom: 68px;
        }
    }
    @media (min-width: @tablet-width) {
        padding: 49px 0 54px;
    }
    @media (min-width: @desktop-width) {
        padding-top: 40px;
        padding-bottom: 0;
    }  
}

.modal__send {
    padding: 44px 0;
    & .modal__text br:nth-of-type(1){
        display: none;
        @media (min-width: @tablet-width) {
            display: block;
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    & .modal__text br:nth-of-type(3){
        display: none;
        @media (min-width: @tablet-width) {
        }
        @media (min-width: @desktop-width) {
            display: block;
        }
    }
    & .modal__text br:nth-of-type(2) {
        @media (min-width: @tablet-width) {
            display: none;
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    & .modal__text br:nth-of-type(4) {
        display: none;
        @media (min-width: @tablet-width) {
            display: block;
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    & .modal__text br:nth-of-type(5) {
        display: none;
        @media (min-width: @tablet-width) {
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    & .modal__text br:nth-of-type(6) {
        display: none;
        @media (min-width: @tablet-width) {
        }
        @media (min-width: @desktop-width) {
            display: block;
        }
    }
    & .modal__text br:nth-of-type(7) {
        display: none;
        @media (min-width: @tablet-width) {
            display: block;
        }
        @media (min-width: @desktop-width) {
            display: none;
        }
    }
    & .modal__text br:nth-of-type(8) {
        @media (min-width: @tablet-width) {
            display: none;
        }
    }
    @media (min-width: @tablet-width) {
        padding-top: 49px;
        padding-bottom: 0;
    }
}
  .modal__wrapper {
    position: relative;
    background: #F2F2F2;
    border-radius: 0px 0px 1px 1px;
    padding: 81px 22px;
    @media (min-width: @desktop-width) {
        border-radius: 0px 0px 5px 5px;
        padding: 81px 22px;
    }
}
.modal__wrapper-error {
    @media (min-width: @desktop-width) {
        border-radius: 0px 0px 5px 5px;
        background: #F2F2F2;
        padding: 53px 0;
    }
}
.modal__send .modal__content {
    padding: 0 22px;
}
.modal__error .modal__content {
    width: 280px;
    @media (min-width: @tablet-width) {
        width: 350px;
        margin: 0 auto;
    }
}
.modal__title {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    margin-bottom: 17px;
    margin-top: 0;
    @media (min-width: @tablet-width) {
        font-size: 30px;
        line-height: 40px;
    }
    @media (min-width: @desktop-width) {
        margin-bottom: 30px;
    }
}
.modal__title  br {
    display: none;
    @media (min-width: @tablet-width) {
        display: block;
    }
    @media (min-width: @desktop-width) {
        display: none;
    }
}
.modal__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    margin-bottom: 30px;
    @media(min-width: @tablet-width) {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 35px;
    }
    @media (min-width: @desktop-width) {
        margin-bottom: 44px;
    }
}
.modal__error .modal__close-button {
    @media(min-width: @desktop-width) {
    width: 296px;
    }
}
.modal__send .modal__close-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-transform: uppercase;
}
.modal__close-button {
    background: #2AAB6D;
    border: 2px solid #2AAB6D;
    border-radius: 1px;
    width: 276px;
    height: 59px;
    color: #FFFFFF;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    @media(min-width: @tablet-width) {
        width: 296px;
        transform: translate(28%, 25%)
    }
    @media(min-width: @desktop-width) {
        width: 456px;
        transform: translate(80%)
    }
}
