.page {
   height: 100%;
   font-size: 18px;
   font-family: "Open Sans";
}
.page__body {
   display: flex;
   flex-direction: column;
   height: 100vh;
   margin: 0;
}
.page__header {
      /* 0 flex-grow, 0 flex-shrink, auto flex-basis */
   flex: 0 0 auto;
}
.page__main {
   flex: 1 0 auto;
   @media (min-width: @desktop-width){
      margin: 0 auto;
   }
}
.page__footer {
   flex: 0 0 auto;
}
