.page-main__header {
    background: #000000 url(../../img/background/back-mobile.jpg) no-repeat;
    max-height: 462px;
    @media (min-width: @tablet-width) {
        background: url(../../img/background/back-tablet.jpg) content-box  no-repeat;
        max-height: 729px;
      }
    @media (min-width: @desktop-width) {
        background: url(../../img/background/back-desktop.jpg) content-box no-repeat;
    }
}
.page-main__subheading {
    padding-top: 102px;
    margin: 0;
    text-align: center;
    color: #FFFFFF;
    font-size: 24px;
    line-height: 33px;
    padding-bottom: 36px;
    font-weight: 300;
    @media (min-width: @tablet-width) {
        padding-top: 190px;
        padding-bottom: 49px;
        font-size: 50px;
        line-height: 68px;
    }
    @media (min-width: @desktop-width) {
        max-width: 747px;
        margin: 0 auto;
        padding-bottom: 90px;
        padding-top: 168px;
    }
}
