.slider__toggles {
    margin: 0;
    padding: 0;
    font-size: 0;
    display: flex;
    justify-content: center;

}

//
.slider-btn-first {
    display: none;
  }
  
  .slider-label-first {
    display: inline-block;
    padding-left: 25px;
    position: relative;
    margin-bottom: 10px;
    cursor: pointer;
  }
  
  .slider-label-first:before {
    content: "";
    display: inline-block;
    width: 6px;
    height: 6px;
    margin-right: 10px;
    position: absolute;
    left: 0;
    bottom: 1px;
    border-radius: 50%;
    border: 2px solid #ccc;
  }
  
  .slider-btn-first:checked + .slider-label-first:before {
    border-color: black;
  }
