.tender {
    line-height: 30px;
    color: #283645;
    font-family: 'Open Sans';
}
.tender__container {
    @media (min-width: @tablet-width) {
        padding: 0 20px;
    }
    @media (min-width: @desktop-width) {
        padding: 0 130px;
    }
}
.tender__wrapper {
    display: grid;
    @media (min-width: @tablet-width) {
        & fieldset {
            border: 2px solid #E5E5E5;
            border-radius: 1px;
            width:  616px;
        }
    }
    @media (min-width: @desktop-width) {
    grid-template-areas: "name apps",
                        "contacts contacts",
                        "achive",
                        "emotion",
                        "send";
    grid-template-columns: 431px 431px;
    grid-template-rows: 297px
                        158px
                        228px
                        213px
                        1fr;
    grid-row-gap: 17px;
    grid-column-gap: 74px;
    }
    & .tender__info{
        padding: 0 20px;
        @media (min-width: @tablet-width) {
            padding: 0;
            order: 1;
        }
        @media (min-width: @desktop-width) {
            grid-area: "name";
            grid-column: 1 / 2;
            grid-row: 1 / 2;
        }
    }
    & .tender__achivements{
        background: #F2F2F2;
        padding: 37px 20px 0 20px;
        @media (min-width: @tablet-width) {
            padding-left: 0;
            padding-right: 0;
        }
        @media (min-width: @desktop-width) {
            padding-top: 0;
            margin-top: 64px;
        }
        & .tender__form-wrap {
            @media (min-width: @tablet-width) {
                margin-left: 0;
            }
        }
        & .tender__title-form {
            @media (min-width: @tablet-width) {
                margin-bottom: 0px;
                width: 435px;
                padding: 0 31px;
            }
            @media (min-width: @desktop-width) {
                margin-left: -32px;
            }
        }
        @media (min-width: @tablet-width) {
            order: 4;
            background: none;
            margin-bottom: 40px;
            }
        @media (min-width: @desktop-width) {
            grid-area: "achive";
            grid-column: 1 / 3;
            grid-row: 3 / 4;
        }
    }
    & .tender__contacts{
        padding: 30px 20px 20px 20px;
        @media (min-width: @tablet-width) {
            order: 3;
            padding: 0;
        }
        @media (min-width: @desktop-width) {
            width: 936px;
            grid-area: "contacts";
            grid-column: 1 / 3;
            grid-row: 2 / 3;
            padding: 0;
            margin-top: 20px;
        }
        & .tender__contact-info {
            @media (min-width: @tablet-width) {
                margin-left: 20px;
                margin-bottom: 10px;
            }
        }
    }
    & .tender__apps{
        @media (min-width: @tablet-width) {
            order: 2;
            }
        @media (min-width: @desktop-width) {
            grid-area: "apps";
            grid-column: 2 / 3;
            grid-row: 1 / 2;
        }

    }
    & .tender__emotion{
        @media (min-width: @tablet-width) {
            order: 5;
            }
        @media (min-width: @desktop-width) {
            grid-area: "emotion";
            grid-column: 1 / 3;
            grid-row: 4 / 5;
            margin-top: 64px;
        }
    }
    & .tender__send{
        padding: 0 20px;
        @media (min-width: @tablet-width) {
            order: 6;
            padding: 0;
            margin-bottom: 20px;
            }
        @media (min-width: @desktop-width) {
            grid-area: "send";
            grid-column: 1 / 3;
            grid-row: 5 / 6;
            margin: 252px 0 0 0;
        }
    }
}
.tender__contacts & .tender__name {
    @media (min-width: @tablet-width) {
    padding: 0 60px;
    }
    @media (min-width: @desktop-width) {
        padding: 0;
    }
}
.tender__contacts .tender__item {
    @media (min-width: @desktop-width) {
        width: 815px;
        padding-left: 60px;
        padding-right: 60px;
    }
}
.tender__aicon {
    @media (min-width: @tablet-width) {
    display: block;
    position: relative;
    display: grid;
        & .tender__contact-info{
            order: 3;
        }
        & .tender__icon{
            order: 2;
            position: absolute;
            top: 0;
            right: -65px;
        }
        & .tender__input{
            order: 1;
            width: 439px;
            @media (min-width: @desktop-width) {
                width: 312px;
            }
        }
        @media (min-width: @desktop-width) {
            &:last-child {
                margin-left: auto;
            }
        }
    }
    @media (min-width: @desktop-width) {
        & .tender__contact-info{
            padding-left: 17px;
        }
    }
}
.tender__aicon input {
    padding-right: 2.5rem;
}
.tender__icon {
    display: none;
    @media (min-width: @tablet-width) {
    display: block;
    position: absolute;
    width: 59px;
    height: 59px;
    background: #E5E5E5;
    cursor: pointer;
        &:hover {
            height: 54px;
        }
    }
    @media (min-width: @desktop-width) {
        left: 310px;
        top: 0px;
        width: 59px;
        height: 59px;
    }
}
.tender__icon svg{
    display: none;
    @media (min-width: @tablet-width) {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    }
}
.tender__info,
.tender__apps,
.tender__emotion,
.tender__send {
    @media(min-width:@tablet-width){
        padding: 0;
      }
    @media(min-width:@desktop-width){
        width: auto;
    }
}
.tender__send .tender__wrap {
    @media(min-width:@tablet-width){
        width: 620px;
        display: flex;
        justify-content: space-between;
    }
    @media(min-width:@desktop-width){
        padding: 0;
        width: auto;
    }
}
.tender__info {
    @media(min-width:@tablet-width){
    width: 620px;
    padding: 0;
    margin: 0;
    & .tender__name {
        margin-right: 17px;
    }
    }
    @media(min-width:@desktop-width){
        width: 431px;
    }
    & .tender__input {
        @media(min-width:@desktop-width){
            width: 336px;
        }
    }
}
.tender__list .tender__item {
    @media(min-width:@desktop-width){
        display: block;
    }
}
.tender__info .tender__list {
    display: flex;
    flex-wrap: wrap;
    border: none;
    padding: 0px 0 20px 0;
    margin: 0;
    @media(min-width:@tablet-width){
        padding: 0;
    }
    @media(min-width:@desktop-width){
        padding: 0;
    }
}
.tender__text {
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    padding: 45px 20px 30px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    margin: 0;
    @media (min-width: @tablet-width) {
        width: 490px;
        margin: 0 auto;
        font-size: 18px;
    }

    @media (min-width: @desktop-width) {
        padding: 100px 0 75px;
        width: 756px;
    }
}
.tender__request {
    display: none;
    @media (min-width: @tablet-width) {
        display: inline;
    }
}
.tender__title {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
}
.tender__title-form {
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    text-align: center;
    color: #D22856;
    margin-bottom: 44px;
    @media (min-width: @tablet-width) {
        padding: 0 25px;
    }
}
.tender__item .tender__title-form {
    display: none;
    width: 256px;
    @media (min-width: @tablet-width) {
        display: block;
    }
}
.tender__form-wrap .tender__title-form {
    width: 218px;
    @media (min-width: @tablet-width) {
        width: 374px;
        font-size: 14px;
    }
}
.tender__apps .tender__title-form {
    width: 178px;
    @media (min-width: @tablet-width) {
        width: 368px;
        }
}
.tender__emotion .tender__title-form {
    width: 234px;
    padding-top: 12px;
    margin-bottom: 22px;
}
.tender__item input {
    z-index: 0;
    font-size: 18px;
    color: #283645;
    width: 236px;
    padding: 20px 20px;
    border: 2px solid rgba(229, 229, 229, 1);
    border-radius: 2px;
    margin-top: 4px;
    margin-bottom: 20px;
    @media (min-width: @tablet-width) {
        width: 517px;
        box-sizing: border-box;
        padding: 17px 20px 17px 20px;
        margin-top: 0;
        margin-bottom: 56px;
    }
    @media (min-width: @desktop-width) {
        width: 324px;
        margin-top: 0;
        margin-bottom: 51px;
    }
    &:hover{
        border-color: #283645;
    }
    &:focus{
        border: 2px solid #D22856 ;
        outline: none;
    }
}
.tender__item span {
    padding-left: 20px;
    display: block;
    @media (min-width: @tablet-width) {
        padding-left: 0px;
        display: inline;
    }
}
.tender__item .tender__input--width {
    @media (min-width: @tablet-width) {
        width: 557px;
        box-sizing: border-box;
    }
    @media (min-width: @desktop-width) {
        width: 376px;
    }
}
.tender__name {
    display: block;
    color: #D22856;
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
        display: grid;
        position: relative;
        width: 371px;
    }
    @media (min-width: @desktop-width) {
        margin-right: 0;
        padding-left: 20px;
        font-size: 14px;
    }
}
.tender__input::placeholder {
    color: rgba(40, 54, 69, 1);
}
.tender__apps {
    margin: 0;
    background: #F2F2F2;
    padding: 42px 20px 0 20px;
    & .tender__wrap {
        @media (min-width: @desktop-width) {
            width: 431px;
        }
    }
    @media (min-width: @tablet-width) {
        background: none;
        width: 620px;
        padding: 0;
        margin-bottom: 65px;
    }
    @media (min-width: @desktop-width) {
        width: 431px;
        padding: 0;
        margin: 0;
    }
    & .tender__radio {
        @media (min-width: @tablet-width) {
            padding: 0 60px;
        }
        @media (min-width: @desktop-width) {
            padding-left: 74px;
        }
    }
}
.tender__form-wrap {
    border: none;
    padding: 0px;
    margin: 0;
    width: 280px;
    @media (min-width: @tablet-width) {
        border: 2px solid #E5E5E5;
        border-radius: 1px;
        margin: 0 20px;
        box-sizing: border-box;
        padding: 35px 60px 0;
    }
    @media (min-width: @desktop-width) {
        display: grid;
        grid-column-gap: 21px;
        grid-template-columns: 257px 257px 257px;
        grid-template-rows: 66px 66px;
        padding: 39px 16px 6px 60px;
        margin: 0;
        grid-column-gap: 44px;
    }
}
.tender__checkbox {
    @media (min-width: @desktop-width) {
    }
    &:nth-of-type(1){
        order: 1;
    }
    &:nth-of-type(2){
        order: 4;
    }
    &:nth-of-type(3){
        order: 2;
    }
    &:nth-of-type(4){
        order: 5;
    }
    &:nth-of-type(5){
        order: 2;
    }
    &:nth-of-type(6){
        order: 6;
    }
}
.tender__title {
    font-size: 14px;
    line-height: 18px;
}
.tender__checkbox,
.tender__radio {
    display: block;
    font-size: 18px;
    line-height: 30px;
    &:last-child{
        padding-bottom: 40px;
    }
}
.tender__checkbox:not(:last-child) {
    margin-bottom: 56px;
    @media (min-width: @tablet-width) {
    margin-bottom: 37px;
    }
}
.tender__radio:not(:last-child) {
    margin-bottom: 29px;
}
.tender__custom {
    position: absolute;
    z-index: -1;
    opacity: 0;
}
.tender__checkbox>span {
    display: inline-flex;
    align-items: center;
    user-select: none;
}
.tender__radio>span {
    display: inline-flex;
    align-items: center;
    user-select: none;
}
.tender__checkbox>span::before {
    content: '';
    display: inline-block;
    width: 35px;
    height: 35px;
    flex-shrink: 0;
    flex-grow: 0;
    margin-right: 22px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    z-index: 0;
    background: #FFFFFF;
    border: 2px solid #E5E5E5;
    border-radius: 2px;
}
.tender__radio>span::before {
    content: '';
    display: inline-block;
    width: 37px;
    height: 37px;
    flex-shrink: 0;
    flex-grow: 0;
    margin-right: 31px;
    background-size: 50% 50%;
    border: 2px solid #E5E5E5;
    background: #FFFFFF;
    border-radius: 50%;
    @media (min-width: @desktop-width) {
        margin-right: 22px;
    }
}
/*      checkbox */
.tender__checkbox>input:not(:disabled):not(:checked)+span:hover::before {
    background: #FFFFFF;
    border: 2px solid #283645;
    border-radius: 1px;
  }
.tender__radio>input:not(:disabled):not(:checked)+span:hover::before {
    border-color: #283645;
}
.tender__icon:hover {
    width: 59px;
    height: 59px;
}
.tender__icon:hover,
.tender__aicon input:hover {
    border: 2px solid #283645;
    border-radius: 1px;
}
  /*     (   ) */
.tender__checkbox>input:not(:disabled):active+span::before {
    background: #FFFFFF;
    border: 2px solid #D22856;
    border-radius: 1px;
}
.tender__radio>input:not(:disabled):active+span::before {
    border-color: #D22856;
}
.tender__checkbox>input:focus+span::before {
    border: 2px solid #283645;
    border-radius: 1px;
}
.tender__radio>input:focus+span::before {
    border-color: #283645;
}
/*   ,    checked */
.tender__checkbox>input:checked+span::before {
    background-color: #FFFFFF;
    background-image: url("../../img/icon/tick.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
}
.tender__radio>input:checked+span::before {
    background-color: #FFFFFF;
    background-image: url("../../img/icon/Ellipse-small.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
}
.tender__contacts .tender__title {
    display: none;
    @media (min-width: @tablet-width) {
    display: block;
    }
}
.tender__contacts .tender__title-form {
    @media (min-width: @desktop-width) {
        margin-bottom: 39px;
    }
}
.tender__contacts .tender__input {
    @media (min-width: @tablet-width) {
        margin-bottom: 10px;
    }
    @media (min-width: @desktop-width) {
        padding-bottom: 17px;
    }
}
.tender__contacts  fieldset {
    padding: 0;
    border: none;
    margin: 0;
    @media (min-width: @tablet-width) {
        width: 616px;
        border: 2px solid #E5E5E5;
        border-radius: 1px;
    }
    @media (min-width: @desktop-width) {
        display: flex;
    }
}
.tender__apps .tender__title {
    margin-bottom: 40px;
    padding-top: 42px;
    @media (min-width: @desktop-width) {
        padding-top: 0;
        margin-bottom: 0;
    }
}
.tender__emotion {
    margin-bottom: 22px;
    padding: 0 20px;
    @media (min-width: @tablet-width) {
    margin-bottom: 70px;
    padding: 0;
    }
    & .tender__wrap {
        @media (min-width: @tablet-width) {
            padding: 42px 60px 53px 60px;
            margin: 0;
        }
        @media (min-width: @tablet-width) {
            padding: 44px 60px 23px;
            width: 416px;
        }
        @media (min-width: @desktop-width) {
            width: 813px;
            margin: 0;

        }
    }
}
.tender__emotion .tender__title {
    margin-bottom: 22px;
    padding-top: 42px;
    font-size: 14px;
    line-height: 18px;
    @media (min-width: @tablet-width) {
        padding-top: 0;
    }
}
.tender__wrap {
    border: none;
    padding: 0;
    margin: 0;
    @media (min-width: @tablet-width) {
        & textarea {
            padding: 23px 20px;
            width: 496px;
            height: 189px;
            &:hover {
                border: 2px solid #283645;
            }
            &:focus {
                border-color: #d22856;
                outline: none;
            }
            @media (min-width: @desktop-width) {
                width: 813px;
                padding-right: 0;
            }   
        }
    }
    @media (min-width: @desktop-width) {
        margin-top: -20px;
    }
}
.tender__area {
    font-size: 18px;
    line-height: 30px;
    border: 3px solid #E5E5E5;
    border-radius: 2px;
    width: 276px;
    padding: 22px 0px 0 20px;
    box-sizing: border-box;
    height: 197px;
    margin-bottom: 15px;
}
.tender__button {
    z-index: 0;
    background: #2AAB6D;
    border: 2px solid #2AAB6D;
    border-radius: 2px;
    width: 276px;
    height: 59px;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    text-transform: uppercase;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 18px;
    &:hover{
        background: #1C9B5E;
        border: 2px solid #1C9B5E;
    }
    &:active{
        background: #1C9B5E;
        border: 2px solid #1C9B5E;
        color: rgba(255, 255, 255, 0.3);
    }
    &:disabled{
        background: #E5E5E5;
        border: 2px solid #E5E5E5;
    }
}
.tender__fill {
    text-align: center;
}
.tender__not-fill{
    display: none;
}
.tender__icon-mail {
    width: 18px;
    height: 13px;
}
.tender__icon-tel {
    width: 20px;
    height: 19px;
}
