.main-nav {
  position: relative;
  min-width: 320px;
  z-index: 3;
}
.main-nav__wrapper {
  margin: 0 auto;
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  &:first-child {
    @media (min-width: @desktop-width) {
      display: grid;
      grid-template-columns: min-content max-content;
      align-items: center;
      width: 1200px;
    }
  }  
}
.main-nav--closed .main-nav__wrapper {
  @media (min-width: @desktop-width) {
    padding-left: 123px;

  }
}
.main-nav--opened .main-nav__wrapper {
  background-color: #283645;
  @media (min-width: @desktop-width) {
    background-color: rgba(0, 0, 0, 0);
  }
}
.main-nav__header {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 66px;
  @media (min-width: @tablet-width) { 
    min-height: 110px;
  }
  @media (min-width: @desktop-width) {
    flex-direction: row;
  }
}
.main-nav--opened .main-nav__header {
  padding: 20px 0 20px 0;
  display: flex;
  align-items: center;
  justify-content: start;
  min-height: 0px;
  border-bottom: 1px solid #000000;
  @media (min-width: @tablet-width) {
    position: relative;
    min-height: 68px;
   }
}
.main-nav__toggle {
  position: absolute;
  width: 68px;
  z-index: 2;
  height: 66px;
  background-color: inherit;
  border: none;
  top: 0;
  right: 0;
  display: block;
  cursor: pointer;
  @media (min-width: @desktop-width) {
    height: 66px;
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  left: auto;
  right: 0;
  @media (min-width: @tablet-width) {
    top: 23px;
  }
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 30px;
    left: 24px;
    width: 23px;
    height: 3px;
    background-color: #ffffff;
  }
  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  &::after {
    transform: rotate(-45deg);
  }
  &:active::before,
  &:active::after {
    background-color: fade(#D22856, 30%);
  }
  &:hover::before,
  &:hover::after {
    background-color: fade(#D22856, 100%);
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 21px;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
  border-radius: 1px;
  @media (min-width: @tablet-width) {
    top: 46px;
  }
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #D22856;
  box-shadow: 0 8px 0 0 #D22856, 0 16px 0 0 #D22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#D22856, 30%);
  box-shadow:
    0 8px 0 0 fade(#D22856, 30%),
    0 16px 0 0 fade(#D22856, 30%);
}
.main-nav--opened .logo--opacity {
  background-color: #283645;
  @media(min-width: @desktop-width) {
    background-color: rgba(0, 0, 0, 0);
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: @desktop-width) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .logo {
  display: inline-block;
  padding-top: 25px;
  padding-bottom: 25px;
  background-color: #283645;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;
  text-align: center;
  @media (min-width: @tablet-width) {
    width: 145px;
    height: 40px;
    border-bottom: none;
  }
  @media (min-width: @desktop-width) {
    padding-top: 0;
    padding-bottom: 25px;
    background-color: rgba(0, 0, 0, 0);
    border-top: none;
    border-bottom: none;
  }
}
.main-nav--nojs .main-nav__wrapper {
  min-height: 0;
  position: static;
  padding: 0px ;
  background: rgba(0, 0, 0, 0.3);
  @media (min-width: @desktop-width) {
    position: absolute;
    padding: 0 130px;
  }
}
.main-nav--nojs .site-list__item {
  display: block;
}
.main-nav--nojs .main-nav__header {
  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: center;
    min-height: auto;
    align-items: none;
    text-align: center;
    background-color: #283645;
    border-bottom: 1px solid #000000;
  }
  @media (min-width: @desktop-width) {
    min-height: 110px;
    background-color: rgba(0, 0, 0, 0);
    border-bottom: none;
  }
}
.main-nav--nojs {
  @media (min-width: @desktop-width) {
      min-height: auto;
    }
}
.main-nav__logo {
  padding-left: 20px;
  cursor: pointer;
  color: #ffffff;
  @media(min-width: @desktop-width) {
    padding-left: 0;
    margin-top: -22px;
    margin-right: 173px;
    background-repeat: no-repeat;
    width: 147px;
    height: 40px;
    &:hover{
        color: #D22856;
    }
    &:active{
        color: #283645;
        opacity: 0.3;
    }
  }
}
.main-nav__logo-desktop {
  display: none;
  @media(min-width: @desktop-width) {
    width: 147px; height: 40px;
    display: block;
  }
}
.main-nav__logo-tablet {
  display: none;
  @media (min-width: @tablet-width) {
    display: block;
    width: 145px; height: 40px;
  }
  @media(min-width: @desktop-width) {
    display: none;
  }
}
.main-nav__logo-mobile {
  width: 74px; height: 23px;
  @media (min-width: @tablet-width) {
    display: none;
  }
}
