.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
ul li {
  list-style-type: none;
}
img {
  max-width: 100%;
  height: auto;
}
*::before, *::after {
  box-sizing: border-box;
}
html, body {
  width: 100%;
}
