.button {
    font-family: 'Open Sans';
    display: block;
    width: 280px;
    border: 3px solid #FFFFFF;
    border-radius: 29px;
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;
    color: #FFFFFF;
    padding: 15px 0px;
    text-decoration: none;
    text-align: center;
    &:hover {
        color: #D22856;
        background: #FFFFFF;
    }
    &:active {
        color: fade(#D22856, 30%);
        background: #FFFFFF;
    }
    @media (min-width: @tablet-width) {
        width: 294px;
        margin-bottom: 23px;
        padding: 21 0;
        font-size: 18px;
      }
    @media (min-width: @desktop-width) {
        font-size: 21px;
        margin-bottom: 69px;
        padding: 20 0;
    }
}
