.table__row {
    display: none;
    @media (min-width: @tablet-width) {  
        display: revert;
    }
    @media (min-width: @desktop-width) {  
        display: revert;
    }
}
.table {
    border: none;
    overflow: hidden;
}
.table__container {
    position: relative;
    padding: 25px 20px 190px;
    background: url(../../img/background/grey-background-mobile.png) no-repeat;
    background-size: cover;
    background-repeat: no-repeat;
    @media (min-width: @tablet-width) {  
        background-position-y: -83px;
        padding-top: 0;
        padding-bottom: 70px;
    }
    @media (min-width: @desktop-width) {
        background-position-y: -140px;
        padding-top: 84px;
        padding-bottom: 137px;
    }
}
.table__wrapper {
    position: relative;
    right: 100%;
    top: 100px;
     border-collapse: collapse;
     margin: 0 auto;
     margin-bottom: 50px;
    & th,
    tr {
        min-width: 276px;
        @media (min-width: @tablet-width) {
            min-width: 152px;
        }
        @media (min-width: @desktop-width) {
            min-width: 234px;
        }
    }
    @media (min-width: @tablet-width) {
        padding: 0 20px;
        right: auto;
    }
    @media (min-width: @desktop-width) {
        padding: 0 130px;
    }
}
.table__header .table__first{
    &:nth-of-type(3) {
        position: relative;
        overflow: hidden;
    }
}
.table__first {
    background: #283645;
    border: 2px solid #283645;
    outline: none;
    border-radius: 1px 0px 0px 0px;
    text-align: center;
}
.table__first--border-head{
    color:#ffffff;
    border-bottom: 1px solid #283645;
    border-right: 2px solid #f2f2f2;
    padding-top: 20px;
    border-left: none;
    font-weight: 700;
    font-size: 16px;
    @media (min-width: @tablet-width) {
        padding-top: 15px;
    }
    @media (min-width: @desktop-width) {
        font-size: 18px;
        padding-top: 20px;
    }
}
.table__first--border-bottom{
    color:#ffffff;
    border-right: 2px solid #f2f2f2;
    padding: 5px 0 30px 0;
    border-bottom: 0px solid #283645;
    border-left: none;
    font-weight: 300;
    font-size: 18px;
    @media (min-width: @tablet-width) {
        padding-bottom: 15px;
        padding-top: 0;
    }
    @media (min-width: @desktop-width) {
        font-size: 24px;
        padding-bottom: 20px;
    }    
}
.table__first--border {
    border-right: 2px solid #f2f2f2;
        border-top: none;
        
}
.table__title {
    display: none;
    @media (min-width: @tablet-width) {
        font-weight: 400;
        line-height: 30px;
        font-size: 14px;
        display: block;
    }
    @media (min-width: @desktop-width) {
        font-size: 18px;
        display: block;
    }
}
.table__title--mobile {
    display: inline-block;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #283645;
    @media (min-width: @tablet-width) {
        display: none;
    }
    @media (min-width: @desktop-width) {
        display: none;
    }
}
.table__title {
    background: #FFFFFF;
    border: 2px solid #E5E5E5;
    border-radius: 2px;
    @media (min-width: @tablet-width) {
        padding: 8px 0px 8px 20px;
    }
    @media (min-width: @desktop-width) {
        padding: 14px 0px 14px 20px;
    }
}
.table__icon {
    background: #FFFFFF;
    border: 2px solid #E5E5E5;
    border-radius: 2px;
    padding: 15px 20px;
    @media (min-width: @tablet-width) {
        text-align: center;
        padding:8px 0px 8px 20px;
    }
    @media (min-width: @desktop-width) {
        padding: 10px 0;
    }
    & svg {
        float: right;
        @media (min-width: @tablet-width) {
        float: none;
        }
    }
}

.table__title--bonus {
    position: absolute;
    top: 10px;
    left: -30px;
    display: block;
    background: #D22856;
    width: 101px;
    height: auto;
    font-weight: 400;
    font-size: 14px;
    padding: 2px;
    transform: rotate(-45deg);
}
.table__toggles {
    position: absolute;
    left: 43%;
    top: 485px;
    @media(min-width:@tablet-width){
        display: none;
    }
}
