.card__wrapper {
    background: url(../../img/background/grey-background-mobile.png) no-repeat;
    background-size: cover;
    background-position-y: 124px;
    @media(min-width:@tablet-width) {
        background: url(../../img/background/grey-background-tablet.png) no-repeat center;
        background-size: cover;
        background-position-y: 194px;
    }
    @media(min-width:@desktop-width){
        background-position-y: 311px;
       padding: 0 130px;
        margin-bottom: 84px;
    }
}
.card__list {
    margin-top: 110px;
    list-style-type: none;
    padding: 0 20px;
    text-align: center;
    &:not(:last-child){
        margin-bottom: 43px;
    }
    @media(min-width:@tablet-width){
        display: grid;
        grid-template-columns: 295px 305px;
        width: 620px;
        grid-template-rows: 140px 140px 195px;
    }
    @media(min-width:@desktop-width){
        grid-template-columns: 300px 300px 300px;
        grid-template-rows: none;
        padding: 0;
        margin-top: 0;
    }
}
.card__item {
    width: 280px;
    margin: 0 auto;
    @media(min-width:@tablet-width){
        width: 305px;
    }
    @media(min-width:@desktop-width){
        padding: 0 24px;
        text-align: center;
        margin-top: 52px;
    }
}
.card__photo {
    display: none;
    @media(min-width:@desktop-width){
        display: block;
        width: 186px;
    }
}

.card__shadow {
    @media(min-width:@desktop-width){
        background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #000000 49.8%, rgba(0, 0, 0, 0) 93.23%);
        opacity: 0.3;
        filter: blur(2px);
        width: 300px;
        height: 17px;
        margin-top: -89px;
    }
}
.card__item {
    text-align: center;
    @media(min-width:@desktop-width){
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.card__title {
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;
    text-align: center;
    color: #000000;
    margin-top: 0;
    margin-bottom: 10px;
    &:nth-child(3) br {
        display: none;
        @media(min-width:@desktop-width){
        display: block;
        }
    }
    @media(min-width:@tablet-width){
        text-align: left;
    }
    @media(min-width:@desktop-width){
        font-size: 30px;
        line-height: 30px;
        margin: 0;
        background: #F2F2F2;
        width: 300px;
        position: relative;
        background: #F2F2F2;
        padding-top: 56px;
        padding-bottom: 30px;
        text-align: center;
        margin-top: -10px;
    }
}
.card__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #283645;
    margin-top: 0;
    margin-bottom: 34px;
    &:nth-of-type(1) br {
        display: none;
        @media(min-width:@tablet-width){
            display: block;
        }
        @media(min-width:@desktop-width){
            display: none;
        }
    }
    &:nth-of-type(1) br:not(:last-child) {
        @media(min-width:@tablet-width){
            display: none;
        }
        @media(min-width:@desktop-width){
            display: block;
        }
    }
    @media(min-width:@tablet-width){
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 0;
        text-align: left;

    }
    @media(min-width:@desktop-width){
        margin: 0;
        text-align: center;
        font-size: 18px;
        line-height: 30px;
        width: 252px;
    }
}
.card__image {
    margin-top: 5px;
    display: inline-block;
    @media(min-width:@tablet-width){
        margin-top: -73px;
        & img {
            display: none;
        }
        & {
            background-image: url(../../img/background/group-mood.png);
            
            background-repeat: no-repeat;
            height: 547px;
            width: 268px;
            grid-column: 1 / 2;
            grid-row: 1 / 4;
        }
    }
    @media(min-width:@desktop-width){
        display: none;
    }
}
.card__item:nth-child(1) {
    background-repeat: no-repeat;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    @media(min-width:@desktop-width){
        grid-column: 1 / 2;
        grid-row: 1 / 2;
    }
}
.card__item:nth-child(2) {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    @media(min-width:@desktop-width){
        grid-column: 2 / 3;
        grid-row: 1 / 2;
    }
}
.card__item:nth-child(3) {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    @media(min-width:@desktop-width){
        grid-column: 3 / 4;
        grid-row: 1 / 2;
    }
}
