.modal-container {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    width: 100%;
    height: 100%;
}
.modal-container__error--close{
    display: none;
}
.modal-container__send--close{
    display: none;
}
.modal-container__error--open{
    display: block;
}
.modal-container__send--open{
    display: block;
}
